.department-box{
    display: flex;
    gap: 1rem;
    padding: 8px 4px;
    // margin-top: 16px;
    &:hover .department-name{
        color: rgb(228, 69, 66);
    }
    .discount{
        position: absolute;
        top: 10px;
        left: 0;
        background-color: #FF0000;
        color: #fff;
        font-size: 15px;
        padding: 6px;
    }
    .department-text{
        display: flex;
        flex: 50%;
        flex-direction: column;
        justify-content: space-between;
        @media (max-width: 767px){
            margin-left: 10px;
            padding: 4px 0;
        }
    }
    .department-description {
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        line-clamp: 2;
        -webkit-box-orient: vertical;
        font-weight: 400;
        min-height: 50px;
        // margin-bottom: 12px;
        line-height: 1.57;
        letter-spacing: 1.57;
        height: auto;
    }
    .department-name{
        font-size: 18px;
        text-transform: capitalize;
        line-height: 1.167;
        @media (max-width: 767px){
            font-size: 14px;
        }
    }
    .department-rassrochka{
        border: 4px solid rgb(254, 238, 0);
        border-radius: 12px;
        text-align: center;
        width: 100%;
        margin: 16px 0;
        @media (max-width: 767px){
            margin: 4px 0;
            border-width: 2px;
            font-size: 14px;
        }
    }
    .department-price{
        display: flex;
        align-items: center;
        justify-content: space-between;
        .price{
            font-size: 18px;
            line-height: 1.2;
            @media (max-width: 767px){
                font-size: 14px;
            }
        }
        .price_old{
            color: rgb(153, 153, 153);
            text-decoration: line-through;
            font-size: 14px;
            line-height: 1.2;
            @media (max-width: 767px){
                font-size: 12px;
            }
        }
    }
}

.department-item{
    @media screen and (max-width: 550px){
        flex-direction: column-reverse;
        row-gap: 20px;
    }
    .item{
        @media screen and (max-width: 550px){
            width: 100%;
        }

    }
    .btnGroup{
        @media screen and (max-width: 550px){

            flex-direction: column
        }
    }

}