.home-slider {
  max-height: 500px;
  overflow: hidden;
  .swiper-slide{
    width: 100% !important;
  }
  .slick-arrow {
    border-radius: 50%;
    z-index: 10;
    width: 42px;
    height: 42px;
    font-size: 16px;
    opacity: 0;
    transition: 0.4s ease 0s;
    circle {
      stroke: #fff !important;
    }
    &:hover {
      background: rgba(255,255,255,0.5);
    }
  }
  &:hover {
    .slick-arrow {
      opacity: 1;
    }
  }
  .imageBox{
    display: flex;
    justify-content: center;
    align-items: center;
    outline: none;
    border: none;
    height: 400px;
    border-radius: 12px;
    width: 100%;
    img {
      // height: 400px;
      height: 400px;
      border-radius: 12px;
      width: 90%;
      object-fit: cover;
      // @media screen and (max-width: 650px){
      //   height: 300px;
      // }
      @media screen and (max-width: 1250px){
        height: 100%;
        object-fit: contain;
      }
    }
    @media screen and (max-width: 1250px){
      height: 100%;
    }
  }
  .slick-prev {
    left: 8px;
  }
  .slick-next {
    right: 8px;
  }
}

.slider {
  max-height: 310px;
  overflow: hidden;
  .slick-arrow {
    border-radius: 50%;
    z-index: 10;
    width: 42px;
    height: 42px;
    font-size: 16px;
    opacity: 0;
    transition: 0.4s ease 0s;
    circle {
      stroke: #fff !important;
    }
    &:hover {
      background: rgba(255,255,255,0.5);
    }
  }
  &:hover {
    .slick-arrow {
      opacity: 1;
    }
  }
  a {
    outline: none;
    border: none;
  }
  img {
    width: 100%;
    object-fit: cover;
  }
  .slick-prev {
    left: 8px;
  }
  .slick-next {
    right: 8px;
  }
}
