.footer {
  background: rgb(245, 245, 246);
  padding-top: 40px;
  color: #333;
  @media screen and (max-width: 910px){
    display: none;
  }
  .footer-logo {
    width: auto;
    height: 50px;
  }
  .footerBody{
    @media screen and (max-width: 910px){
      display: none;
    }
  }
  .footer__link {
    margin-bottom: 1rem;
    font-size: 15px;
    a {
      display: block;
      line-height: 1.5;
    }
    &:hover a {
      color: #e44542;
    }
  }
  .copyright {
    margin-top: 40px;
    padding: 40px 0;
    color: #333;
    background: rgb(234, 234, 234);
    img {
      width: 100px;
      min-width: 100px;
      height: 25px;
      object-fit: contain;
    }
  }
}
