.category-slider {
  padding: 50px 0;
  @media screen and (max-width: 850px){
    padding: 20px 0;
  }
  @media screen and (max-width: 550px){
    padding: 0;
  }
  &:hover .slick-arrow {
    opacity: 1 !important;
  }
  .category-box {
    padding: 14px 16px;
    margin: 0 8px;
    display: flex !important;
    align-items: center !important;
    justify-content: space-between ;
    column-gap: 4px;
    padding: 0;
    margin: 0 4px;
    border-radius: 8px;
    background: #f5f5f6;
    // flex-direction: column-reverse;
    flex-direction: column;
    min-width: 200px;
    @media screen and (max-width: 850px){
      min-width: 100%;
    }
    &:hover > div {
      color: rgb(228, 69, 66);
    }
    img {
      height: 96px;
      width: 96px;
      min-width: 96px;
      object-fit: contain;
      margin-left: auto;
      @media (max-width: 767px) {
        height: 56px;
        width: 56px;
        min-width: 56px;
        margin-left: 0;
      }
    }
    > div {
      text-transform: capitalize;
      margin-bottom: 12px;
      text-align: center;
      @media (max-width: 767px) {
        font-size: 14px;
        overflow: hidden;
        text-align: left;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
      }
      @media screen and (max-width: 650px)
      {
        font-size: 12px
      }
      @media screen and (max-width: 430px){
        font-size: 10px;
      }
    }
  }
  .slick-arrow {
    width: 32px;
    height: 32px;
    border-radius: 50%;
    z-index: 10;
    opacity: 0;
    fill: rgb(177, 177, 177);
    background: #fff;
    transition: all 0.3s;
  }
  .slick-next {
    right: -10px;
  }
  .slick-prev {
    left: -10px;
  }
}
