.contactUs{
    padding: 50px 0;
    .contactUsTitle{
        margin-bottom: 10px;
        max-width: 700px;
        .title{
            @media screen and (max-width: 420px){
                font-size: 16px;
            }
        }
    }
    .form{
        display: flex;
        flex-direction: column;
        padding: 10px;
        border-radius: 6px;
        max-width: 700px;
        .btn{
            background: #00ad00;
            color: #fff;
            padding: 10px;
            border-radius: 6px ;
        }
        .inputBox{
            display: flex;
            flex-direction: column;
            row-gap: 5px;
            label{
                color: #000;
            }
            input,
            textarea{
                width: 100%;
                padding: 5px;
                margin: 10px 0;
                border: none;
                background: transparent;
                border: 1px solid #c1c1c1 ;
                border-radius: 6px;
                color: #000;
                outline: none;
                resize: none;
                &:focus ~ span,
                &:valid ~ span{
                    color: #000;
                    transform: translateY(-24px);
                }
                &::placeholder{
                    font-size: 16px;
                    font-weight: 400;
                }
                    
            }
                
        }
    }
}